.photo-box{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.photo-box div{
    width: 100%;
    margin: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    @media only screen and (max-width: 900px) {
        flex-direction: column;
    }
    @media only screen 
        and (width: 375px) 
        and (height: 667px) 
        and (-webkit-device-pixel-ratio: 2) { 
            flex-direction: column;
        }
    @media only screen 
        and (width: 414px) 
        and (height: 896px) 
        and (-webkit-device-pixel-ratio: 3) {
            flex-direction: column;
        }
     @media only screen 
